import React from "react";
import { Image, Grid, Header, Popup, Segment, Button, Container, Icon, Message } from "semantic-ui-react";
import { REACT_APP_API_STATIC, REACT_APP_MAIN_URL } from "../../utils/consts";
import ImageGallery from "react-image-gallery";
import "./DescAppView.css";

let DescView = ({appInfo, message, installLoading, user, installHendler, visitSite, CopyToClipboard, copy, setCopy})=>{
    
    return(
        <Segment className="appPageSegment" style={{maxWidth : "750px"}}>
            <Grid>
                <Grid.Row>
                    <Grid.Column width={5} centered="true">
                        <Image
                            src={`${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${appInfo.app_id}/logo.png`}
                            className="appDescMainImage"
                        />
                        <Button
                            disabled={message.critical || installLoading || !user._appInstallSupport}
                            className="installDescButton"
                            onClick={installHendler}
                            loading={installLoading}
                            primary
                        >
                            <Icon name='arrow alternate circle down outline'/>
                            <>Установить</>
                        </Button>
                    </Grid.Column>
                    <Grid.Column width={11}>
                        <div
                            className="descHeaderBlock"
                        >
                            <Header as="h1">
                                {appInfo.app_name}
                            </Header>
                            <div className="appPageCategory">{appInfo.category_name}</div>
                        </div>
                        <div
                            className="buttonDescBlock"
                        >
                            <Popup content='Перейти на сайт приложения' trigger={<Button
                                    className="secondaryDescPageButton"
                                    icon="external alternate"
                                    onClick={visitSite}
                                />}
                            />
                            <Popup content='Копировать ссылку на приложение' trigger={
                                <CopyToClipboard
                                    text={copy.value}
                                    onCopy={() => setCopy({status : true})}
                                >
                                    <Button
                                        className="secondaryDescPageButton"
                                        icon={copy.status ? "checkmark" : "copy"}
                                        disabled={copy.status}
                                    />
                                </CopyToClipboard>
                            } />
                        </div>
                        {message.header &&
                            <Message 
                                negative={message.type === 'negative'}
                                className="messageBlock"
                            >
                                <Message.Header>{message.header}</Message.Header>
                                <p>{message.message}</p>
                            </Message>
                        }
                        <Container
                            className="descriptionBlock"
                        >
                            <p>{appInfo.app_description}</p>
                        </Container>
                        {appInfo.screenShots &&
                            <div
                                style={{marginTop : '30px'}}
                            >
                                <ImageGallery
                                    lazyLoad={true}
                                    items={appInfo.screenShots}
                                    showFullscreenButton={false}
                                    showPlayButton={false}
                                    showBullets={true}
                                    autoPlay={true}
                                />
                            </div>
                        }
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            {/*
            
            
            <div
                className="appPageDescInnerBlock"
            >
                <Image
                    size="medium"
                    src={`${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${appInfo.app_id}/logo.png`}
                    className="appDescMainImage"
                />
                <Button
                    disabled={message.critical || installLoading || !user._appInstallSupport}
                    className="installDescButton"
                    onClick={installHendler}
                    loading={installLoading}
                    primary
                >
                    <Icon name='arrow alternate circle down outline'/>
                </Button>
                <div>
                    <Header as="h1">
                        {appInfo.app_name}
                    </Header>
                    <div className="appPageCategory">{appInfo.category_name}</div>
                </div>
                
                {message.header &&
                    <Message 
                        negative={message.type === 'negative'}
                        className="messageBlock"
                    >
                        <Message.Header>{message.header}</Message.Header>
                        <p>{message.message}</p>
                    </Message>
                }
                <div
                    className="buttonBlock"
                >
                    <Popup content='Перейти на сайт приложения' trigger={<Button
                            className="secondaryPageButton"
                            icon="external alternate"
                            onClick={visitSite}
                        />}
                    />
                    <Popup content='Копировать ссылку на приложение' trigger={
                        <CopyToClipboard
                            text={copy.value}
                            onCopy={() => setCopy({status : true})}
                        >
                            <Button
                                className="secondaryPageButton"
                                icon={copy.status ? "checkmark" : "copy"}
                                disabled={copy.status}
                            />
                        </CopyToClipboard>
                    } />
                </div>
                <Container
                    className="descriptionBlock"
                >
                    <p>{appInfo.app_description}</p>
                </Container>
               
                {appInfo.screenShots &&
                    <div
                        style={{marginTop : '15px'}}
                    >
                        <ImageGallery
                            lazyLoad={true}
                            showThumbnails={false}
                            items={appInfo.screenShots}
                            showFullscreenButton={false}
                            showPlayButton={false}
                            showBullets={true}
                            showNav={false}
                            autoPlay={true}
                        />
                    </div>
                }
            </div>
            */}
        </Segment>
    );
};

export default DescView;