import React from "react";
import { Image, Header, Popup, Segment, Button, Container, Icon, Message } from "semantic-ui-react";
import { REACT_APP_API_STATIC, REACT_APP_MAIN_URL } from "../../utils/consts";
import ImageGallery from "react-image-gallery";
import "./MobileAppView.css";

let MobileView = ({appInfo, message, installLoading, user, installHendler, visitSite, CopyToClipboard, copy, setCopy})=>{
    
    return(
        <Segment className="appPageSegment" style={{maxWidth : "500px"}}>
            <div
                className="appPageMobileInnerBlock"
            >
                <Header as="h1">
                    {appInfo.app_name}
                </Header>
                <div style={{color : 'rgba(0,0,0,.4)', fontSize:'1em', margin : '-12.5px 0 15px'}}>{appInfo.category_name}</div>
                <Image
                    size="small"
                    src={`${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${appInfo.app_id}/logo.png`}
                    className="appMobileMainImage"
                />
                {message.header &&
                    <Message 
                        negative={message.type === 'negative'}
                        className="messageBlock"
                    >
                        <Message.Header>{message.header}</Message.Header>
                        <p>{message.message}</p>
                    </Message>
                }
                <div
                    className="buttonMobileBlock"
                >
                    <Button
                        disabled={message.critical || installLoading || !user._appInstallSupport}
                        className="installButton"
                        onClick={installHendler}
                        loading={installLoading}
                        primary
                    >
                        <Icon name='arrow alternate circle down outline'/>
                    </Button>
                    <Popup content='Перейти на сайт приложения' trigger={<Button
                            className="secondaryMobilePageButton"
                            icon="external alternate"
                            onClick={visitSite}
                        />}
                    />
                    <Popup content='Копировать ссылку на приложение' trigger={
                        <CopyToClipboard
                            text={copy.value}
                            onCopy={() => setCopy({status : true})}
                        >
                            <Button
                                className="secondaryMobilePageButton"
                                icon={copy.status ? "checkmark" : "copy"}
                                disabled={copy.status}
                            />
                        </CopyToClipboard>
                    } />
                </div>
                <Container
                    className="descriptionBlock"
                >
                    <p>{appInfo.app_description}</p>
                </Container>
                {appInfo.screenShots &&
                    <div
                        style={{marginTop : '25px'}}
                    >
                        <ImageGallery
                            lazyLoad={true}
                            showThumbnails={false}
                            items={appInfo.screenShots}
                            showFullscreenButton={false}
                            showPlayButton={false}
                            showBullets={true}
                            showNav={false}
                            autoPlay={true}
                        />
                    </div>
                }
            </div>
        </Segment>
    );
};

export default MobileView;