import { makeAutoObservable } from "mobx";
import apps from "../data/appList.json";
import categories from "../data/categoryList.json";

export default class AppStore{
    constructor(){
        this._appList = prettyList();
        makeAutoObservable(this);
    }
};
let prettyList = ()=>{
    categories.forEach(item=>{
        item.apps.forEach((app, index)=>{
            apps.forEach(storeApp=>{
                if(app === storeApp.app_id){
                    storeApp['category_name'] = item['category_name'];
                    item.apps[index] = storeApp;
                }
            })
        });
    });
    return categories;
}