import React, { useEffect, useState, useContext } from "react";
import { useParams } from "react-router-dom";
import { Segment, List, Header } from "semantic-ui-react";
import { Context } from "../../index";
import AppLoading from "../../components/AppLoading";
import CategoryListItem from "../../components/CategoryListItem";
import NotFound from "../../pages/NotFound/NotFound";

import "./CategoryPage.css";

let CategoryPage = ()=>{

    let { app } = useContext(Context);
    let { id } = useParams();
    let [ categoryList, setCategoryList ] = useState({});
    let [ pageLoading, setPageLoading ] = useState(true);
    let [ noFound, setNoFoundCategory ] = useState(false);

    useEffect(()=>{
        let categoryIndex = app._appList.findIndex(x=>x.category_id === id);
        if(categoryIndex !== -1){
            document.title = `PwaBox - Категоря ${categoryList.category_name}`;
            setCategoryList(app._appList[categoryIndex]);
        } else setNoFoundCategory(true);
        setPageLoading(false);
    },[app, categoryList, id]);

    return(
        <>
            {pageLoading && <AppLoading/>}
            {!pageLoading &&
                <Segment className="mainCategoryBlock">
                    {noFound && <NotFound type="category"/>}
                    {!noFound &&
                        <>
                            <Header>
                                {categoryList.category_name}
                            </Header>
                            <List divided verticalAlign='middle' size='large'>
                                {categoryList.apps.map((item, index)=>{
                                    return <CategoryListItem key={index} data={item} index={index}/>
                                })}
                            </List>
                        </>
                    }
                </Segment>
            }
        </>
    );
}
export default CategoryPage;