import React from "react";
import { Icon } from "semantic-ui-react";

let Footer = ()=>{
    
    return(
        <div style={{
            margin:'45px auto',
            maxWidth : '750px',
            width : '100%',
            padding : '15px'
        }}>
            <div style={{
                "display": "inline-block"
            }}>
                <a
                    href="https://mobile.twitter.com/i1fxx"
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{"marginRight" : "10px"}}
                >
                    <Icon
                        name='twitter'
                        size='big'
                    />
                </a>
            </div>
            <div style={{
                "display": "inline-block",
                "float" : "right"
            }}>
                 pwabox © {new Date().getFullYear()}
            </div>
      </div>
    )
};

export default Footer;