import React, { useEffect, useContext, useState } from 'react';
import { Icon, Header, List } from 'semantic-ui-react';
import { Link } from "react-router-dom";
import { Context } from "../../index";

let NotFound = ({type})=>{
    let { category } = useContext(Context);
    let [ pageText, setPageText ] = useState('');
    useEffect(()=>{
        let pageText;
        switch(type){
            case("app"):{
                pageText = 'Приложение не найдено!';
                break;
            }
            case("category"):{
                pageText = 'Категиория не найдена!';
                break;
            }
            default:{
                pageText = 'Страница не найдена!';
            }
        }
        setPageText(pageText);
        document.title = `PwaBox - ${pageText}`;
    },[type]);
    return(
        <div style={{textAlign : 'center'}}>
            <div style={{margin : "45px 0 45px 0"}}>
                <Header icon>
                    <Icon name="find" />
                    {pageText}
                </Header>
            </div>
            <List horizontal>
                <List.Item as={Link} to="/">Главная страница </List.Item>
                <List.Item>|</List.Item>
                {category._categoryList.map((cat, catIndex)=>{
                    return <List.Item key={catIndex} as={Link} to={`../category/${cat.category_id}`}>{cat.category_name}</List.Item>
                })}
            </List>
        </div>
    )
};

export default NotFound;