import React from "react";
import { List, Image } from 'semantic-ui-react';
import { Link } from 'react-router-dom';

let CategoryListItem = ({data, index})=>{
    return(
        <List.Item>
            <Link
                as="div"
                to={`../app/${data.app_id}`}
                style={{display : 'flex'}}
            >
                <Image
                    size="tiny"
                    src={`../assets/app/${data.app_id}/logo.png`}
                    style={{borderRadius: ".28571429rem"}}
                />
                <List.Content style={{ paddingLeft : '8px'}}>
                    <List.Header>{data.app_name}</List.Header>
                    #{index+1}
                </List.Content>
            </Link>
        </List.Item>
    );
}
export default CategoryListItem;