import React, { useEffect, useState, useContext } from "react";
import { Icon, Grid, Header } from "semantic-ui-react";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";
import { Context } from "../../index";
import AppLoading from "../../components/AppLoading";
import AppItem from "../../components/AppItem";
import "./MainPage.css";

let MainPage = observer(()=>{
    let [ appList, setAppList ] = useState([]);
    let { app, user } = useContext(Context);
    let [ loading, setPageLoading ] = useState(true);
    

    useEffect(()=>{
        document.title = "PwaBox - Главная";
        user.regManifest('../site.webmanifest');
        setAppList(app._appList);
        setPageLoading(false);
    },[app, user]);

    return(
        <div className="mainPageSegment">
            {loading && <AppLoading/>}
            {!loading &&
                <>
                    <Link
                        to="../category/top-10"
                    >
                        <div
                            className="mainPageImage"
                        >
                            Топ 10
                        </div>
                    </Link>
                    {appList.slice(1).map((data, index)=>{
                        return <div key={index} className="appsList">
                            <Header
                                className="categoryHeader"
                                as="h2"
                            >
                                {data.category_name}
                            </Header>
                            {data.apps.length > 4 &&
                                <div className="allLink">
                                    <Link
                                        to={`../category/${data.category_id}`}
                                    >
                                        Все&nbsp;
                                        <Icon name="arrow right"/>
                                    </Link>
                                </div>
                            }
                            <Grid
                                className="categoryAppList"
                                columns={4}
                            >
                                {data.apps.slice(0, 4).map((app, appIndex)=>{
                                    return  <Grid.Column
                                        key={appIndex}
                                        style={{minWidth:'180px'}}
                                    > 
                                        <Link
                                            to={`../app/${app.app_id}`}
                                            style={{
                                                display: 'inline-block',
                                                textAlign: 'center',
                                                padding: '14px'
                                            }}
                                        >
                                            <AppItem info={app}/>
                                        </Link>
                                    </Grid.Column>
                                })}
                            </Grid>
                        </div>
                    })}
                </>
            }
        </div>
    );
});

export default MainPage;