import React, { useEffect, useContext, useState } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { Context } from "./index";
import { osName, browserName } from "react-device-detect";
import AppRouter from "./components/AppRouter";
import NavBar from "./components/NavBar";
import Footer from "./components/Footer";
import AppLoading from "./components/AppLoading";
import "./App.css";
import "semantic-ui-css/semantic.min.css";
//import "react-image-gallery/styles/css/image-gallery.css";

let App = ()=> {

  let {user} = useContext(Context);
  let [loading, setLoading] = useState(true);

  useEffect(()=>{
    let acceptList = ['Linux/Chrome', 'Windows/Chrome','Windows/Edge', 'Mac OS/Chrome', 'Mac OS/Edge', 'iOS/Mobile Safari', 'Android/Chrome', 'Android/Samsung Browser'];
    user.setSupport(acceptList.indexOf(osName+'/'+browserName) === -1 ? false : true);
    user.setStatus(window.matchMedia('(display-mode: minimal-ui)').matches);
    user.setUserLanguadge((navigator.language || navigator.userLanguage).split('-')[0]);
    setLoading(false);
  },[user]);
  
  return (
    <Router>
      {loading && <AppLoading/>}
      {!loading && 
        <>
          <NavBar/>
          <AppRouter/>
          <Footer/>
        </>
      }
    </Router>
  );
}

export default App;
