import {makeAutoObservable} from "mobx";
export default class UserStore{
    constructor(){
        this._appEvent = {};
        this._appInstalled = false;
        this._appInstallSupport = false;
        this._userLanguadge = "ru";
        makeAutoObservable(this);
    }
    setStatus(e){
        this._appInstalled = e;
    }
    setSupport(e){
        this._appInstallSupport = e;
    }
    setUserLanguadge(e){
        this._userLanguadge = e;
    }
    regManifest(url){
        let listener = (e)=>{
            e.preventDefault();
            this._appEvent = e;
        }
        window.removeEventListener("beforeinstallprompt", listener, false);
        document.getElementById("manifestLink").href = url;
        window.addEventListener("beforeinstallprompt", listener, false);
    }
};